.HomeNav{
    display:flex;
    background-color:#F6F3ED;
    height:4rem;
    color:#030303;
    box-shadow:1px 1px #C5C6D0;
}

#mainTitle{
    font-size:32px;
}
.mr-auto{
    font-size:24px;
}

#dateLabel{
    font-size:20px;
    margin:0px 12px;
}

#userInfo{
    display:flex;
    align-content:center;
    padding:0.5rem 1rem;
    height:3rem;
}

#userName{
    font-size:20px;
    margin-right:12px;
}

#logoutBtn{
    cursor:pointer;
    color:#BC544B;
    padding-top:3px;
}

@media all and (min-width:0px) and (max-width:1280px){
    .mr-auto{
        display:flex;
        font-size:12px;
    }
    #mainTitle{
        font-size:24px;
    }
    #dateLabel{
        font-size:12px;
    }
    #userInfo{
        margin-top:10px;
    }
    #userName{
        font-size:14px;
    }
    #logoutBtn{
        font-size:12px;
    }

}

#dateDiv{
    display:flex;
    background:white;
    padding:1px 3px;
    margin-right:30px;
    border-radius:10px;
    color:#494848;
    box-shadow:0.3px 0.5px gray;
    align-items:center;

}


#order{
  position: relative;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  min-height: 670px;
  margin-top:5px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 5px;
  display:flex;
  justify-content: center;
}

#orderLoading{
  position: relative;
  background-color: white;
  padding-top:200px;
}

#orderLeft{
  position: relative;
  display:flex;
  width:50%;
  border-right: 4px solid #EAEAEA;
}

#tables{
  position:relative;
  text-align: center;
  padding-top:20px;
  padding-bottom: 20px;
}

#orderRight{
  position:relative;
  width:50%;
  display:flex;
  float:right;
}

#takeOut{
  text-align: center;
  width:100%;

}

#toOrders{
  border-top:2px solid  #EAEAEA;
  position: relative;
  display:flex;
  justify-content:center;
  flex-wrap: wrap;
}
#tableBtn{
  position:relative;
    margin-top:40px;
    margin-left:35px;
    margin-right: 35px;
    margin-bottom: 40px;
    width: 130px;
    height:130px;
    color: white;
    background-color: #816C5B;
    border-color: #816C5B;
    border-radius:30px;
    text-align: center;
    box-shadow: 3px 3px #524539;
}

#takeOutBtn{
  position:relative;
  margin-top:20px;
  width:150px;
  height:70px;
  border-radius: 20px;
  font-weight: bold;
  background-color: #74D5DD;
  border-color: #74D5DD;
  color:#2F66A9;
}


#modalContent{
    display:flex;
    flex-direction:row;
    position: relative;
}

.selectedFoods, .servingFoods{
  width:50%;
  marginRight:5px;
  border:1px solid #EBECF0;
  box-shadow:1px 2px #BEBEBE;
  border-radius:5px;
}

#modalContent h2{
  text-align:center;
  border-bottom:1px solid #949494;
}

.selectedFoods .orderList{
  display:flex;
  flex-direction:column;

}

.orderList .anOrderItem{
  display:flex;
  align-items:center;
  margin-left:5px;
  margin-right:5px;
  color:#7E7E7E;
}

#total{
  text-align:center;
  float:bottom;
}

#total > b{
  font-size:24px;
}

.servingFoods > div {
  margin:8px;
  text-align:center;
  position:relative;
}

.menuBtn{
  background-color:white;
  border:transparent;
  outline:0;
  box-shadow:0.5px 1px #BEBEBE;
  border-radius:5px;
  margin:2px;
  padding:0px;
}

.foodImg{
  width:70px;
  height:70px;
  object-fit:cover;
}

.foodInfo{
  display:flex;
  flex-direction:column;
  background-color:#F5F5F5;
  padding:0.2rem 0.5rem;
  align-items:center;
  justify-content:center;
}

#curState1{
  display:flex;
  flex-direction:column;
  align-items:center;
  width:100%;
  height:50px;
  margin-top:20px;
  background-color: #F6F3ED;
  color:#494848;
  border-radius: 20px;
}

#curState2{
  display:flex;
  flex-direction:column;
  width:100%;
  height:50px;
  margin-top:20px;
  background-color: #cfebfd;
  color:#030303;
  border-radius: 20px;
}

#curState3{
  display:flex;
  flex-direction:column;
  align-items:center;
  width:100%;
  height:50px;
  margin-top:20px;
  background-color: #B1DDA1;
  color:#3d5324;
  border-radius: 20px;
}

.modal-dialog{
    top:70px;
}

#modal-foot{
    justify-content: unset;
    display:unset;
    position: relative;
}

#deleteFromAdd{
    margin-left:5px;
    background-color: #99aab5;
    border-color: #99aab5;
    color:black;
    height:20px;
    padding:5px;
    font-size: 10px;
}

@media all and (min-width:0px) and (max-width:1000px){
  #tableBtn{
      position:relative;
      margin-top:20px;
    margin-left:20px;
    margin-right: 20px;
    margin-bottom: 60px;
      width: 6rem;
      height:6rem;
      border-radius:20px;
      text-align: center;
      box-shadow: 3px 3px #524539;
      font-size:12px;
  }

  #takeOutBtn{
    width:120px;
    height:50px;
    border-radius: 15px;
    font-size:12px;
  }
  #curState1,#curState2,#curState3{
    border-radius:30px;
    margin-top:10px;
  }
  #curState3{
    padding-top:4px;
    font-size:12px;
  }

  #modalContent{
    font-size:8px;
  }

  #modalContent h2{
    font-size:18px;
  }
  .orderList{
    font-size:10px;
  }
  #total > b{
    font-size:14px;
  }
  .menuBtn{
    min-width:60px;
  }
  .foodImg{
    width:55px;
    height:55px;
  }
  .foodInfo label{
    font-size:12px;
    height:0.7rem;
  }
  #modal-foot button{
    font-size:14px;
  }
}
.orderCard_Clerk{
    margin:15px;
    border-radius: 10px;
    box-shadow: 2px 2px gray;
    cursor:pointer;
}

#takeOuts:hover{
    transform:scale(1.1);
    transition-duration:0.3s;
    transition-timing-function:ease-in-out;
}

.orderCard_Clerk > card-header{
    max-height:40px;
    font-size:14px;
}

.orderCard_Clerk >.card-body, .orderCard_Clerk > .card-footer{
    padding:0.5rem;
}

.orderCard_Clerk > .card-body{
    min-height:6.45rem;
}

.orderCard_Clerk > .card-body label{
    font-size:14px;
}

@media all and (min-width:0px) and (max-width:1280px){
    .orderCard_Clerk> .card{
        width:8rem;
    }
    .orderCard_Clerk > .card-header{
        font-size:12px;
    }
    .orderCard_Clerk > .card-body{
        min-height:5.9rem;
    }
    .orderCard_Clerk > .card-body label{
        font-size:12px;
    }

    .orderCard_Clerk >.card-footer{
        font-size:13px;
    }
}
